import React from "react";
import { Card, Button, Accordion, Row, Col } from "react-bootstrap";
import PropTypes from "prop-types";
import LastVisited from "./LastVisited";
import Leaderboards from "./modal/Leaderboards";
import { useDispatch } from "react-redux";
import * as examActions from "../../redux/actions/exam";

const FreestyleCard = React.memo(({ topicName, topicId, freestyle }) => {
  const dispatch = useDispatch();

  const setExamInRedux = async (obj) => {
    dispatch(examActions.setExam(obj));
  };

  return (
    <Card>
      <Card.Header style={{ backgroundColor: "#e7edee" }}>
        <Accordion.Toggle
          as={Button}
          variant="link"
          eventKey="freestyle"
          className="text-danger"
        >
          <i className="fas fa-bolt mr-2" />
          <strong>Modo rpido</strong>
        </Accordion.Toggle>
      </Card.Header>
      <Accordion.Collapse eventKey="freestyle">
        <Card.Body>
          <h2 className="mb-3">Modo rpido</h2>
          {/* description */}
          <strong style={{ fontSize: "14px" }} className="mb-2 mt-2 d-block">
            Este es el examen modo rpido
          </strong>
          {/* last visited */}
          <LastVisited date={freestyle.lastVisit} />
          <br />
          {/* duration */}
          <span style={{ fontSize: "14px" }} title="Duracin">
            <i className="fas fa-stopwatch mr-2" />
            {freestyle.timer + " mins."}
          </span>
          <br />
          <Leaderboards top10={freestyle.top10} />
          <br />
          {/* columns */}
          <Row className="my-3">
            <Col className="text-center">
              <h1 className="mb-0 text-danger">
                <span title="Puntuacin ms alta">
                  {freestyle.myHighestScore}
                </span>
              </h1>
              <h4>
                <small className="text-muted">Puntos</small>
              </h4>
            </Col>
            <Col className="text-center">
              <h1 className="mb-0 text-danger">
                <span title="Nmero de intentos">{freestyle.myTryouts}</span>
              </h1>
              <h4>
                <small className="text-muted">Intentos</small>
              </h4>
            </Col>
          </Row>
          {/* button */}
          <Button
            variant="danger"
            className="shadow-sm"
            onClick={() =>
              setExamInRedux({
                duration: freestyle.timer,
                name: "Modo rpido",
                topicName: topicName,
                topicId: topicId,
              })
                .then(() => (window.location.href = "/course/freestyle"))
                .catch((err) => {
                  console.log("error", err);
                  alert("Ocurri un error inesperado");
                  window.location.href = "/course";
                })
            }
          >
            Iniciar
          </Button>
        </Card.Body>
      </Accordion.Collapse>
    </Card>
  );
});

FreestyleCard.propTypes = {
  freestyle: PropTypes.object.isRequired,
  topicName: PropTypes.string.isRequired,
};

export default FreestyleCard;
