import React, { useState, useEffect } from "react";
import { Layout } from "../../components/Layout";
import ScrollButton from "../../components/scrollbutton";
import { Row, Col, Container, Button, Spinner } from "react-bootstrap";
import CourseInfoCard from "./components/CourseInfoCard";
import API from "../../utils/API";
import "./courseinfostyle.scss";

const CourseInfo = React.memo((props) => {
  const [courses, setCourses] = useState();

  useEffect(() => {
    API.fetchCoursesBySchool(props.routeProps.match.params.course)
      .then((res) => setCourses(res.data))
      .catch((err) => {
        console.log(err);
        alert("Ocurri un error, actualiza la pgina");
      });
  }, [props.routeProps.match.params.course]);

  const setDescription = () => {
    switch (props.routeProps.match.params.course) {
      case "Primaria":
        return "Compuesto por las bases de la Aritmtica este curso es ideal para que los alumnos desde 3er grado hasta 6to desarrollen y practiquen sus habilidades lgico-matemticas, as como para los estudiantes        que necesiten fortalecer sus bases en Aritmtica puedan disponer del curso en cualquier momento del da.";
      case "Secundaria":
        return "La secundaria es una de las etapas ms importantes de la vida ya que es donde empieza la transicin de nio a adulto; las matemticas pueden parecer igual de confusas para un adolescente, sin embargo este curso ha sido diseado de una forma tal que el alumno aprenda y refuerce los conocimientos bsicos del lgebra y est preparado para enfrentar los cursos posteriores en preparatoria.";
      case "Preparatoria":
        return "Independientemente de la carrera por la cual se llegue a decidir, la Trigonometra, lgebra y Estadstica son de los retos que el estudiante en Preparatoria puede llegar a enfrentar, el curso ha sido pensado para acompaar, practicar y aprender estos temas que lo ayudarn a prepararse de tal forma que en su vida universitaria el alumno pueda llevar una vida equilibrada entre asignaciones.";
      case "Universidad":
        return "lorem ipsum";
      default:
        return null;
    }
  };

  return (
    <Layout backgroundColor="white">
      <Container
        style={{
          paddingTop: "40px",
          marginBottom: "80px",
        }}
      >
        {/* back button */}
        <Button className="backToHomeBttn" href="/">
          <i className="fas fa-long-arrow-alt-left mr-2" />
          <span>Regresar</span>
        </Button>
        {/* title */}
        <Row>
          <Col md={{ span: 8, offset: 2 }}>
            <div className="text-center">
              <h1 className="courseinfoheader">
                {props.routeProps.match.params.course}
              </h1>
            </div>
            <p className="lead text-left text-md-center mt-3">
              {setDescription()}
            </p>
          </Col>
        </Row>
        {/* courses */}
        <div className="d-flex flex-wrap justify-content-center my-3">
          {courses ? (
            courses.length ? (
              courses.map((c) => (
                <CourseInfoCard
                  key={c._id}
                  courseId={c._id}
                  lessonCounter={c.topics.length}
                  title={c.name}
                  price={c.price}
                  topics={c.topics.reduce((acc, cv) => {
                    acc.push(cv.name);
                    return acc;
                  }, [])}
                />
              ))
            ) : (
              <></>
            )
          ) : (
            <div className="my-4 text-center" style={{ marginTop: "150px" }}>
              <Spinner animation="border" role="status">
                <span className="sr-only">Loading...</span>
              </Spinner>
            </div>
          )}
        </div>
      </Container>
      <ScrollButton scrollStepInPx={150} delayInMs={16.66} />
    </Layout>
  );
});

export default CourseInfo;
