import React from "react";
import { Jumbotron, Button, Container, Row, Col } from "react-bootstrap";
import Fade from "react-reveal/Fade";
import "./welcomejumbotron.scss";

const MyJumbotron = React.memo(() => {
  return (
    <Jumbotron fluid className="wj_jumboStyle">
      <Container>
        <Row>
          <Col md={{ offset: 2, span: 7 }}>
            {/* title */}
            <Fade bottom cascade>
              <h2 className="text-left wj_title mb-0">MATEMTICAS</h2>
              <h2 className="text-left wj_title">SIMPLIFICADAS</h2>
              {/* list */}
              <p className="mb-1 mt-3 mt-lg-0">
                <i className="fas fa-plus-circle wj_plusIcon" />
                <span className="wj_text">
                  Cursos de matemticas para estudiantes de cualquier grado
                  educativo
                </span>
              </p>
              <p className="mb-1">
                <i className="fas fa-plus-circle wj_plusIcon" />
                <span className="wj_text">
                  Miles de ejercicios con explicaciones y videos tutoriales
                </span>
              </p>
              <p className="mb-1">
                <i className="fas fa-plus-circle wj_plusIcon" />
                <span className="wj_text">
                  Aprendiendo matemticas a tu propio ritmo y con un mtodo
                  nico
                </span>
              </p>
            </Fade>
            {/* button */}
            <Button
              size="lg"
              className="shadow mt-4 wj_registerButton"
              href="/signup"
            >
              Regstrate
            </Button>
          </Col>
        </Row>
      </Container>
    </Jumbotron>
  );
});

export default MyJumbotron;
