import React from "react";
import { CardDeck, Container } from "react-bootstrap";
import Fade from "react-reveal/Fade";
import SectionTitle from "../SectionTitle";
import OfferCard from "./components/OfferCard";
import "./threecolumns.scss";

const ThreeColumns = React.memo(() => {
  return (
    <Container className="tc_container">
      <Fade>
        <SectionTitle
          title="Qu Ofrecemos?"
          text="En MeXmticas contamos con tres servicios para as poder llegar a ms
          estudiantes a nivel local y nacional."
        />
        <CardDeck>
          <OfferCard
            img="/images/laptop.png"
            title="Cursos en Lnea"
            text="Una manera innovadora de aprender matemticas, nuestra
                plataforma en lnea se acopla al programa educativo de la SEP."
          />
          <OfferCard
            img="/images/document.png"
            title="Mtodo MeXmticas"
            text="La mejor manera de aprender matemticas desde sus cimientos, para
            que as seas el nmero uno en tu clase."
          />
          <OfferCard
            img="/images/chat.png"
            title="Clases de Regularizacin"
            text="Abarcamos temas de matemticas para alumnos de cualquier
            nivel educativo, de manera presencial o en lnea."
          />
        </CardDeck>
      </Fade>
    </Container>
  );
});

export default ThreeColumns;
