import React from "react";
import { Button } from "react-bootstrap";
import PropTypes from "prop-types";
import TeacherAPI from "../../../../utils/TeacherAPI";

const DeleteRewardBttn = React.memo(({ filePath, courseId, topicId }) => {
  const handleDelete = () => {
    TeacherAPI.t_deleteReward({
      filePath,
      courseId,
      topicId,
    })
      .then((res) => {
        console.log(res.data);
        alert("La imagen de recompensa ha sido eliminada con xito.");
        window.location.reload();
      })
      .catch((err) => {
        console.log(err);
        alert("Ocurri un error, vuelve a intentarlo.");
      });
  };

  return (
    <Button
      size="sm"
      variant="transparent"
      className="ml-1 genericDeleteButton"
      title="Eliminar"
      onClick={handleDelete}
    >
      <i className="fas fa-trash" />
    </Button>
  );
});

DeleteRewardBttn.propTypes = {
  filePath: PropTypes.string.isRequired,
  courseId: PropTypes.string.isRequired,
  topicId: PropTypes.string.isRequired,
};

export default DeleteRewardBttn;
