import React from "react";
import { Button, Form, Col, InputGroup } from "react-bootstrap";
import PropTypes from "prop-types";
import { Formik, ErrorMessage } from "formik";
import * as yup from "yup";
import TeacherAPI from "../../../utils/TeacherAPI";

const ExamDurationForm = React.memo(
  ({ formLabel, formInitialText, examId }) => {
    const yupschema = yup.object({
      newDuration: yup
        .number()
        .positive("El nmero debe ser mayor a 1")
        .required("Requerido"),
    });

    return (
      <Formik
        initialValues={{
          newDuration: formInitialText,
        }}
        validationSchema={yupschema}
        onSubmit={(values, { setSubmitting }) => {
          setSubmitting(true);
          values.examId = examId;
          //
          TeacherAPI.t_updateExamDuration(values)
            .then((res) => {
              console.log(res.data);
              alert("La duracin del examen fue actualizada con xito.");
              window.location.reload();
            })
            .catch((err) => {
              alert("Ocurri un error. Vuelve a intentarlo.");
              setSubmitting(false);
              console.log(err);
            });
        }}
      >
        {({
          values,
          errors,
          touched,
          handleChange,
          handleBlur,
          handleSubmit,
          isSubmitting,
        }) => (
          <Form noValidate onSubmit={handleSubmit}>
            {/* name */}
            <Form.Row>
              <Form.Group as={Col}>
                <Form.Label>{formLabel}</Form.Label>
                <InputGroup className="mb-3">
                  <Form.Control
                    maxLength="40"
                    type="number"
                    name="newDuration"
                    value={values.newDuration}
                    onChange={handleChange}
                    onBlur={handleBlur}
                    isValid={touched.newDuration && !errors.newDuration}
                    isInvalid={touched.newDuration && !!errors.newDuration}
                  />
                  <InputGroup.Append>
                    <InputGroup.Text id="basic-addon2">minutos</InputGroup.Text>
                  </InputGroup.Append>
                </InputGroup>
                <ErrorMessage
                  className="text-danger"
                  name="newDuration"
                  component="div"
                />
              </Form.Group>
            </Form.Row>
            {/* buttons */}
            <Form.Group className="text-right">
              <Button variant="dark" type="submit" disabled={isSubmitting}>
                Guardar
              </Button>
            </Form.Group>
          </Form>
        )}
      </Formik>
    );
  }
);

ExamDurationForm.propTypes = {
  formLabel: PropTypes.string,
  formInitialText: PropTypes.number,
  courseId: PropTypes.string,
  topicId: PropTypes.string,
};

export default ExamDurationForm;
